:root {
  /* if u want to change the color of
   * the ripple change this value
  */
  --color-ripple: rgba(255,255,255,0.8); }

[data-animation="ripple"] {
  position: relative;
  /*Position relative is required*/
  outline: none;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent; }

*[data-animation="ripple"]:focus {
  outline: none; }

*[data-animation="ripple"]::selection {
  background: transparent;
  pointer-events: none; }

/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after {
  content: '';
  content: none; }

q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

@font-face {
  font-family: 'marion_regular';
  src: url("../fonts/marion/marion-webfont.woff2") format("woff2"), url("../fonts/marion/marion-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

