.nav-toggle {
  position: absolute;
  top: 10px;
  right: 10px;
  display: flex;
  align-items: center;
  width: 30px;
  height: 30px;
  padding: 0;
  border: none;
  box-shadow: none;
  outline: none;
  background-color: transparent;
  font-size: 0;
  cursor: pointer;
  z-index: 20; }
  .nav-toggle__line {
    position: relative;
    display: block;
    height: 4px;
    width: 100%;
    background-color: #ead493; }
    .nav-toggle__line:before, .nav-toggle__line:after {
      position: absolute;
      top: 8px;
      left: 0;
      content: '';
      height: 4px;
      width: 100%;
      background-color: inherit; }
    .nav-toggle__line:after {
      top: -8px; }
  .nav-toggle:hover .nav-toggle__line {
    background-color: #e4dcd9; }
  @media screen and (min-width: 768px) {
    .nav-toggle {
      display: none; } }

.start-screen {
