.videoplayer {
  position: relative;
  opacity: 0;
  animation: a_fade-in 1s ease-out 1.2s forwards; }
  .videoplayer__stream {
    width: 100%;
    background-color: #e6dedb; }
  .videoplayer__play {
    position: absolute;
    left: 50%;
    top: 50%;
    width: 76px;
    height: 76px;
    transform: scale(0.7) translate(-50%, -50%);
    transform-origin: 50%, 50%;
    background-color: #ead493;
    font-size: 1px !important; }
    .videoplayer__play:before {
      content: '';
      position: absolute;
      top: 24px;
      left: 31px;
      border: 25px solid transparent;
      border-top-width: 15px;
      border-bottom-width: 15px;
      border-left-color: #474747;
      transform: scale(1);
      transform-origin: 8px 50%;
      transition: border-color .5s, transform .5s; }
    .videoplayer__play:hover:before {
      border-left-color: #ead493; }
    .videoplayer__play:after {
      content: '';
      position: absolute;
      top: 26px;
      left: 26px;
      width: 24px;
      height: 24px;
      border-radius: 2px;
      background-color: #474747;
      transition: background-color .5s, transform .5s;
      transform: scale(0.4); }
    .videoplayer__play:hover:after {
      background-color: #ead493; }
    .videoplayer__play.playing:before {
      transform: scale(0.6); }
    .videoplayer__play.playing:after {
      transform: scale(1); }
  .videoplayer__promo {
    position: absolute;
    bottom: -20%;
    right: 0;
    max-width: 25%;
    opacity: 0;
    animation: a_fade-in .8s ease-out 2.2s forwards; }
  .videoplayer .arc {
    position: relative;
    width: 100%;
    left: 3vw; }
  @media screen and (min-width: 768px) {
    .videoplayer__play {
      transform: scale(1) translate(-50%, -50%); }
    .videoplayer__promo {
      bottom: -35%;
      right: 10%; } }

