.mr-1 {
  margin-right: 1em;
}

.mr-2 {
  margin-right: 2em;
}

.mb-2 {
  margin-bottom: 2em;
}

.text-left {
  text-align: left;
}

.hidden-s {
  @media screen and (max-width: $tablet-s - 1) {
    display: none;
  }
}

.bold-text {
  font-weight: 700;
}

.fade {
  opacity: 0;
  /*transform: translateY(50px);*/
  transition: opacity 1s ease-out;
}
.fade-in {
  opacity: 1;
  /*transform: translateY(0);*/
}

.hide-tablet {
  @media screen and (max-width: $tablet - 1) {
    display: none!important;
  }
}
