
.container {
  max-width: $containerMaxWidth;
  margin-left: auto;
  margin-right: auto;
  padding-left: 10px;
  padding-right: 10px;
}

html {
  box-sizing: border-box;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

html, body {
  margin: 0;
  height: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  background-color: $body-bg;
  color: $text-color;
  font-family: $main-font, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

.section {
  padding-top: 60px;
  padding-bottom: 60px;

  &--gradient {
    background-image: linear-gradient(rgba(255, 255, 255, .01), $gray_2);
  }

  @media screen and (min-width: $tablet-s) {
    padding-top: 5vw;
    padding-bottom: 5vw;
  }
}

.l-content {
  position: relative;
}

.header {

  &__nav {
    font-family: $main-title-font;

    a {
      padding: 1.2vw calc(5px + 1vw);
      color: $text-color;
      text-decoration: none;
      font-size: 16px;
      font-weight: bold;
      letter-spacing: .05em;
      transition: color .3s, background-color .3s;

      &:hover,
      &.active {
        background-color: $gray;
        color: $white;
      }
    }
  }

  &__img {
    display: block;
    width: 20vw;
    min-width: 150px;
    max-width: 310px;
    padding: 2vw;
  }

  @media screen and (max-width: $tablet-s - 1) {
    &__nav {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      padding: 50px 0;
      background-color: $gray;
      z-index: 1;

      a {
        display: block;
        padding: 15px;
        text-align: center;

        &:hover,
        &.active {
          background-color: $gold;
          color: $text-color;
        }
      }

      &:not(.visible) {
        display: none;
      }
    }
  }

  @media screen and (min-width: $tablet-s) {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    z-index: 10;

    &__nav {
      display: flex;
      justify-content: flex-end;
      padding: 2vw;
    }

    &__img {
      display: block;
      margin-left: auto;
    }
  }
}

.nav-toggle {
  position: absolute;
  top: 10px;
  right: 10px;
  display: flex;
  align-items: center;
  width: 30px;
  height: 30px;
  padding: 0;
  border: none;
  box-shadow: none;
  outline: none;
  background-color: transparent;
  font-size: 0;
  cursor: pointer;
  z-index: 20;

  &__line {
    position: relative;
    display: block;
    height: 4px;
    width: 100%;
    background-color: $gold;

    &:before,
    &:after {
      position: absolute;
      top: 8px;
      left: 0;
      content: '';
      height: 4px;
      width: 100%;
      background-color: inherit;
    }
    &:after {
      top: -8px;
    }
  }

  &:hover {
    .nav-toggle__line {
      background-color: $gray_0;
    }
  }

  @media screen and (min-width: $tablet-s) {
    display: none;
  }
}

.start-screen {
  padding: 3vw;

  &__video {
    margin-top: 5vw;
  }

  .logo {
    display: block;
    text-align: center;
  }

  @media screen and (min-width: $tablet-s) {
    display: flex;

    &__text {
      display: flex;
      flex-direction: column;
      flex-basis: 50%;
      min-width: 50%;
      padding-left: 3vw;
    }

    &__video {
      display: flex;
      justify-content: flex-start;
      margin-top: 0;
      align-items: flex-end;
      position: relative;
      flex-basis: 50%;
      min-width: 50%;
      padding-right: calc(50% - 700px);

      &:before {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: $gold;
        transform-origin: 100% 0;
        transform: translate(3vw, -3vw) scale(0);
        will-change: scale;
        animation: a_scale-video .3s ease-in .6s forwards;
      }
    }

    .logo {
      text-align: left;
    }
  }

  @media screen and (min-width: $desktop) {
    min-height: calc(100vh - 80px);
  }
}

@keyframes a_scale-video {
  from {
    transform: translate(3vw, -3vw) scale(0);
  }

  to {
    transform: translate(3vw, -3vw) scale(1);
  }
}

.main-text {
  &__title {
    position: relative;
    margin-top: calc(3vw + 20px);
    margin-bottom: 3vw;
    font-size: 40px;
    line-height: 1.2;
    font-family: 'marion_regular';
    font-weight: 400;
    letter-spacing: .05em;
    opacity: 0;
    transform: translateX(-100px);
    animation: a_fade-in-left .3s ease-in .6s forwards;

    > b {
      font-weight: 600;
    }

    &:before {
      content: '';
      position: absolute;
      bottom: -3vw;
      left: 0;
      height: 2px;
      width: 150px;
      background-color: $gold;
      background-image: linear-gradient($gray);
      background-size: 30px 100%;
      background-repeat: no-repeat;
    }
  }

  &__description {
    padding: 3vw 2vw 4vw 0;
    font-size: 16px;
    line-height: 2.3;
    letter-spacing: .1em;
    opacity: 0;
    transform: translateX(-100px);
    animation: a_fade-in-left .3s ease-in 1s forwards;
  }

  &__button {
    opacity: 0;
    transform: translateX(-100px);
    animation: a_fade-in-left .3s ease-in 1.6s forwards;
  }

  &--mobile {
    margin-top: 2vw;
    text-align: center;
  }

  @media screen and (min-width: $tablet-s) {
    &__title {
      &:before {
        left: -30px;
      }
    }

    &--mobile {
      display: none;
    }
  }

  @media screen and (min-width: $tablet) {
    &__title {
      font-size: 52px;
    }

    &__description {
      font-size: 18px;
    }
  }
}

.videoplayer {
  position: relative;
  opacity: 0;
  animation: a_fade-in 1s ease-out 1.2s forwards;

  &__stream {
    width: 100%;
    background-color: $gray_1;
  }

  &__play {
    position: absolute;
    left: 50%;
    top: 50%;
    width: 76px;
    height: 76px;
    transform: scale(.7) translate(-50%, -50%);
    transform-origin: (50%, 50%);
    background-color: $gold;
    font-size: 1px !important;

    &:before {
      content: '';
      position: absolute;
      top: 24px;
      left: 31px;
      border: 25px solid transparent;
      border-top-width: 15px;
      border-bottom-width: 15px;
      border-left-color: $gray;
      transform: scale(1);
      transform-origin: 8px 50%;
      transition: border-color .5s, transform .5s;
    }

    &:hover:before {
      border-left-color: $gold;
    }

    &:after {
      content: '';
      position: absolute;
      top: 26px;
      left: 26px;
      width: 24px;
      height: 24px;
      border-radius: 2px;
      background-color: $gray;
      transition: background-color .5s, transform .5s;
      transform: scale(.4);
    }

    &:hover:after {
      background-color: $gold;
    }

    &.playing:before {
      transform: scale(.6);
    }

    &.playing:after {
      transform: scale(1);
    }
  }

  &__promo {
    position: absolute;
    bottom: -20%;
    right: 0;
    max-width: 25%;
    opacity: 0;
    animation: a_fade-in .8s ease-out 2.2s forwards;
  }

  .arc {
    position: relative;
    width: 100%;
    left: 3vw;
  }

  @media screen and (min-width: $tablet-s) {
    &__play {
      transform: scale(1) translate(-50%, -50%);
    }

    &__promo {
      bottom: -35%;
      right: 10%;
    }
  }
}

.button {
  min-width: 12em;
  padding: 1.4em;
  border: none;
  background-size: 300% 100%;
  background-image: linear-gradient(to right, $gray 40%, $gold 60%);
  color: $white;
  font-size: 12px;
  font-weight: bold;
  letter-spacing: .1em;
  text-transform: uppercase;
  cursor: pointer;
  transition: background-position .5s, color .5s;

  &:focus,
  &:active {
    outline: none;
  }

  &:hover {
    color: $gray;
    background-position: 100% 50%;
  }

  &--round {
    min-width: initial;
    border-radius: 50%;
  }

  &--inverse {
    background-position: 100% 50%;
    color: $gray;

    &:hover {
      color: $gold;
      background-position: 0 50%;
    }
  }

  @media screen and (min-width: $tablet-s) {
    font-size: 14px;
  }

  @media screen and (min-width: $desktop) {
    font-size: 18px;
  }
}

.logo-small {
  display: block;
  max-width: 80px;

  img {
    width: 100%;
  }
}

.main-title {
  margin-bottom: .5em;
  text-align: center;
  font-size: 30px;
  font-family: 'marion_regular';
  letter-spacing: .04em;

  > b {
    font-weight: bold;
    letter-spacing: .075em;
  }

  @media screen and (min-width: $tablet) {
    font-size: 38px;
  }
}

.main-title-description {
  text-align: center;
  font-size: 16px;
  opacity: .4;
  letter-spacing: .05em;

  @media screen and (min-width: $tablet) {
    font-size: 20px;
  }
}

.text-shadow {
  text-shadow: 2px 2px 5px rgba(0, 0, 0, .65);
}

.slick-slider {
  overflow: hidden;
}

.slick-loading .slick-list {
  background: $white url('../img/ajax-loader.gif') center center no-repeat !important;
}

.section {
  .ps__rail-x {
    opacity: 1!important;
    background-color: #cecece!important;
    border-radius: 4px;
  }

  .ps__thumb-x {
    background-color: $gold;
    height: 11px;
  }
}

@media screen and (max-width: $tablet-s) {
  .arc {
    display: none;
  }
}

@keyframes a_fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes a_fade-in-left {
  from {
    opacity: 0;
    transform: translateX(-100px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
