:root {
  /* if u want to change the color of
   * the ripple change this value
  */
  --color-ripple: rgba(255,255,255,0.8);
}


[data-animation="ripple"] {
  position: relative; /*Position relative is required*/
  outline: none;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
}

*[data-animation="ripple"]:focus {
  outline: none;
}

*[data-animation="ripple"]::selection {
  background: transparent;
  pointer-events: none;
}
*[data-animation="ripple"]:hover {
  //background: linear-gradient(100deg, #e570e7 0%,#79f1fc 100%);
}
